body {
  position: relative;
  font-family: $common-fonts;

}

* {
  outline: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 18px;
}

a {
  //color: $lightGreen;
  &:hover {
    text-decoration: none;
  }
  &:visited {
    text-decoration: none;
  }
  &:link{
    text-decoration: none;
  }
  &:active{
    text-decoration: none;
  }
}

p {
  margin: 0;
}

.hideScroll {
  overflow: auto;
  position:fixed;

}

.time{
  text-align: center;
  padding: 30px;
  text-align: center;
  color:$white;
  font-size:54px;
  line-height: 1.2;
  opacity:0.9
}

.wrapper__panel{
  display: flex;
  padding: 0 24px;
}
