.quest{

  &__panel{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
     max-width: 1200px;
    margin: auto;
    width: 100%;
    padding: 0 70px 0 100px;

  }
  &__question{
    padding: 6px 68px 12px 24px ;
    background: black;
    color: white;
    font-family: Roboto;
    font-weight: bold;
    font-size: 22px;
    line-height: 31px;
    display: flex;
    align-items: center;
    width: 100%;
    text-align: center;
  }

  &__list{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 22px;
  }
  &__answer{
    flex-basis: 48%;
    margin-bottom: 22px;
    padding: 30px 90px 34px ;
    color: white;
    background: black;
    font-family: Roboto;
    font-weight: bold;
    color: white;
    text-align: center;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: .3s;
    position: relative;
    cursor:pointer;

    &.active{
      color: black;
      background: white;
      cursor:pointer;
      transform: translate(10px, -10px);
    }
    &.dis{
      pointer-events: none;
    }
  }
}