.modal{
  position: absolute;
  max-width: 911px;
  width: 100%;

  background: rgba(196, 196, 197,  0.4);
  transform: translate(-50%,-50%);
  top: 50%;
  left:50%;
  z-index: 1;
  &__wrapper{
    padding: 30px 59px 22px 39px;
  }
  &__label{
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    text-transform: lowercase;
    text-align: center;
    color: #FFFFFF;
    padding-bottom: 13px;
  }
  &__line{
    margin-top: 150px;
  justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__anhors{
    display: flex;
    justify-content: space-between;
    margin-top: 103px;

    a {
      font-weight: bold;
      font-size: 20px;
      line-height: 24px;
      text-transform: lowercase;
      color: black;
    }
  }
  &__list{
    display: flex;
  }
  &__item{
    box-shadow: 0px 0px 4px rgba(255, 255, 255, 0.1);
    border-radius: 6px;
    width: 16px;
    height: 3px;
    margin-left: 2px;
    background: rgba(196, 196, 197,  0.4);
    &.active{
      box-shadow: 0px 0px 4px #FFFFFF;
      background: #FFFFFF;
    }
  }
}