.panel-right{
  &__list{
    display: flex;
    flex-direction: column;
  }

  @keyframes fadeScale {
    from{
  transform: scaleY(1);
  }
  to{
    transform: scaleY(0);
  }
}


  &__item{
    max-height: 30px;
    height: 30px;
    width: 4px;
    background: transparent;
    margin-bottom: 4px;
    position: absolute;
    position: relative;
    transform: scaleY(1);
    transform-origin: bottom;
    &:after{
      content: '';
      position: absolute;width: 100%;
      top: 0;
      left: 0;
      height: 100%;
      background: aliceblue;
      z-index: 1;
      transform-origin: bottom;
      transform: scaleY(1);

    }
    &.active{
      animation: fadeScale 15s  ease-in forwards;



    }

    &.dis{
      &:after{
        background: transparent;
      }
    }
  }
}