.header{
  padding: 35px 51px;

  &__wrapper{
    display: flex;
    justify-content: space-between;
  }
  &__volum{
    cursor: pointer;
    position: relative;
    &:after{
      content:"" ;
      width: 1px;
      height: 100%;
      position: absolute;
      background: $white;
      top: 0;
      transform: rotate(45deg);
      left: 50%;
      transition: .3s;
    }
    &_click{
      &:after{
        background: transparent;
      }
    }
  }

  &__burger{
    position: relative;
    width: 44px;
    height: 12px;

  }
  &__line{
    background: $white;
    width: 100%;
    height: 2px;
    position: absolute;
    transition: .3s;
    &:first-child{
      top: 0;
    }
    &:last-child{
      bottom:0;
    }
    &__open{
      &:first-child{
        top: 0;
        transform: rotate(45deg);

      }
      &:last-child{
        bottom: 9px;
        transform: rotate(-45deg);
        left: 2px;
      }
    }


  }
}
